.calendar-container {
  font-family: Arial, sans-serif;
  padding: 20px;
  max-width: 100%;
  margin: 0 auto;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
}

.date-cell {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  background: #f9f9f9;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.date-cell:hover {
  background: #f1f1f1;
}

.expense-input {
  margin-top: 10px;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  width: 80%;
  max-width: 400px;
}

.modal-buttons button {
  margin: 5px;
}

.summary {
  margin-top: 20px;
  font-size: 18px;
}

.day {
  padding: 2px;
}

.date-number {
  color: red;
}

@media (max-width: 768px) {
  .calendar-grid {
    grid-template-columns: repeat(4, 1fr);
  }

  .date-cell {
    padding: 8px;
    font-size: 12px;
  }

  .modal {
    width: 90%;
  }
}

@media (max-width: 480px) {
  .calendar-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .modal {
    width: 95%;
    padding: 15px;
  }

  .date-number {
    font-size: 16px;
  }

  .summary {
    font-size: 16px;
  }
}
